@charset 'utf-8';

.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    height: 28px;
    margin: 14px 0 12px;
    line-height: normal;
    text-align: center;
    font-size: 0;
}

.tui-pagination .tui-page-btn.tui-prev {
    width: 82px;
}

.tui-pagination .tui-page-btn.tui-next {
    width: 78px;
}

.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    margin: 0 4px 0 4px;
    width: 33px;
    height: 31px;
    padding: 6px 0 6px;
    //  margin-left: 0;
    color: #333;
    font-size: 16px;
    font-weight: normal;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    background: #F4F4F4;
    border: 1px solid #4440F6;
    border-radius: 2px;
}

.tui-pagination .tui-page-btn:hover {
    //  background-color: #f4f4f4;
    cursor: pointer;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: #4440F7;
    border: 1px solid #4440F6;
    border-radius: 2px;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    //  background-color: #4b96e6;
    cursor: pointer;
}

